import React from 'react'
import styled from 'styled-components'
import Spinner from '@atlaskit/spinner'

const Container = styled.div`
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
`

export default (props) => (
    <Container>
        <Spinner {...props} />
    </Container>
)


