import React, {PureComponent} from 'react';
import PropTypes from 'prop-types';
import { pdfjs, Page } from 'react-pdf';
import Loader from './Loader'

pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.js`;

class PageRenderer extends PureComponent {
    static propTypes = {
        index: PropTypes.number.isRequired,
        style: PropTypes.object.isRequired,
        data: PropTypes.object.isRequired
    }

    componentDidMount() {
        console.log('componentDidMount')
    }

    render() {
        const { index, data, style } = this.props;
        const { scale } = data;

        return (
            <div style={style}>
                <div style={{ display: 'flex', justifyContent: 'center', padding: 32, alignItems: 'center', height: '100%', boxSizing: 'border-box', overflow: 'hidden' }}>
                    <Page
                        pageNumber={this.props.index + 1}
                        scale={scale}
                        loading={<Loader />}
                        renderAnnotationLayer={false}
                        onLoadError={error => console.error(error) /* eslint-disable-line no-console */}
                    />
                </div>
            </div>
        );
    }
}

export default PageRenderer;


